import React from "react"
import { Link, graphql } from "gatsby"

import Bio from "../components/bio"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { rhythm } from "../utils/typography"

const messages = {
  en: {
    allPostsTitle: `All posts`,
  },
  ru: {
    allPostsTitle: `Все записи`,
  }
}

class BlogIndex extends React.Component<any> {
  render() {
    const { langKey } = this.props.pathContext;
    const { data } = this.props
    const posts = data.allMarkdownRemark.edges;
    const { allPostsTitle } = messages[langKey];

    return (
      <Layout location={this.props.location} langKey={langKey}>
        <SEO title={allPostsTitle} langKey={langKey} />
        <Bio langKey={langKey} />

        {/* <TagsList /> */}
        {posts.map(({ node }) => {
          const title = node.frontmatter.title || node.fields.slug
          return (
            <article key={node.fields.slug}>
              <header>
                <h3
                  style={{
                    marginBottom: rhythm(1 / 4),
                  }}
                >
                  <Link style={{ boxShadow: `none` }} to={node.frontmatter.url}>
                    {title}
                  </Link>
                </h3>
                <small>{node.frontmatter.date}</small>
              </header>
              <section>
                <p
                  dangerouslySetInnerHTML={{
                    __html: node.frontmatter.description || node.excerpt,
                  }}
                />
              </section>
            </article>
          )
        })}
      </Layout>
    )
  }
}

export default BlogIndex

export const pageQuery = graphql`
  query($langKey: String!) {
    allMarkdownRemark(
      filter: { frontmatter: {
        langKey: { eq: $langKey }
        type: { eq: "post" }
      } }
      sort: { fields: [frontmatter___date], order: DESC }
    ) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            url
            date(formatString: "MMMM DD, YYYY")
            title
          }
        }
      }
    }
  }
`
