/**
 * Bio component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react"
import { useStaticQuery, graphql, Link } from "gatsby"
import styled from "styled-components";
import Image from "gatsby-image"
import { rhythm } from "../utils/typography"
import { SocialWithMessage } from "./social";

const MOBILE_WIDTH = 600;
const MOBILE_QUERY = `(max-width: ${MOBILE_WIDTH}px)`;

const Wrapper = styled.div`
  display: flex;
  margin-bottom: ${rhythm(2.5)};

  @media ${MOBILE_QUERY} {
    flex-direction: column;
  }
`;

const ImageStyled = styled(Image)`
  &&& {
    margin: 0 ${rhythm(0.5)} 0 0;
    border-radius: 50%;

    @media ${MOBILE_QUERY} {
      margin: 0 auto ${rhythm(0.5)}
    }
  }
`;

const messages = {
  en: {
    writtenBy:
      `In this blog I'm sharing my experience, thoughts and ideas on many different topics ` +
      `such as software developement, traveling, books, lifestyle, finances, tech and more. `,// +
      // `Subscribe to social media, RSS or push-notifications to keep in touch!`,
    readInDiffLang: `Этот блог также доступен на русском`
  },
  ru: {
    writtenBy:
      `В этом блоге я делюсь своим опытом, мылями и идеями по многим темам, которые мне интересны: ` +
      `разработка ПО, путешествия, книги, лайфстайл и многое другое. `,// +
      // `Подпишись в соц.сетях, RSS или на push-оповещения, чтобы быть на связи!`,
    // writtenBy: `Сергей по профессии - программист. Когда не путешествует, то большую часть времени живет в Санкт-Петербурге, работая удаленно на стартапы со всего мира.`,
    readInDiffLang: `This blog is also available in English`
  }
}

const Bio = ({ langKey = 'en' }: any) => {
  const data = useStaticQuery(graphql`
    query BioQuery {
      avatar: file(absolutePath: { regex: "assets/profile-pic.jpg/" }) {
        childImageSharp {
          fixed(width: 400) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      site {
        siteMetadata {
          author
          social {
            twitter
          }
        }
      }
    }
  `)

  const { author } = data.site.siteMetadata;
  const diffLangHomepage = langKey === 'en' ? '/ru' : '/';

  return (
    <Wrapper>
      <ImageStyled
        fixed={data.avatar.childImageSharp.fixed}
        alt={author}
        style={{
          minWidth: 100,
          minHeight: 100,
          width: 100,
          height: 100,
        }}
        imgStyle={{
          borderRadius: `50%`,
        }}
      />
        <div>
          <p>
            {messages[langKey].writtenBy.replace('%s', author)}
            {` `}<br/>
            <Link to={diffLangHomepage}>
              {messages[langKey].readInDiffLang}
            </Link>.
          </p>

          <SocialWithMessage langKey={langKey} />
        </div>
    </Wrapper>
  )
}

export default Bio
